"use client";
import React, { useEffect, useMemo, useState } from "react";
import { AffiliateAPI } from "@/utils/api";
import { Skeleton } from "@/components/ui/skeleton";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { useToast } from "@/components/ui/toast";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Copy, DollarSign, Link2, Sparkles, Users, Wallet } from "lucide-react";

type Dashboard = {
  referrals_count: number;
  amount: number;
  percent: number;
  year: number;
  month: number;
  tenants: string[];
  payouts: { pending_total: number; paid_total: number };
};

type LinkItem = { token: string; url: string; active: boolean };

function formatCurrency(amount: number) {
  return new Intl.NumberFormat("en-US", { style: "currency", currency: "USD" }).format(amount || 0);
}

export default function AffiliateOverviewPage() {
  const { show } = useToast();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [dash, setDash] = useState<Dashboard | null>(null);
  const [links, setLinks] = useState<LinkItem[]>([]);
  const [canCreateMore, setCanCreateMore] = useState(true);
  const [payoutMonth, setPayoutMonth] = useState("");
  const [payoutPercent, setPayoutPercent] = useState<number>(5);
  const [requestingPayout, setRequestingPayout] = useState(false);

  async function refreshAll() {
    setLoading(true);
    try {
      const [d, l] = await Promise.all([
        AffiliateAPI.dashboard(),
        AffiliateAPI.getLinks(),
      ]);
      const dashData = d as Dashboard;
      setDash(dashData);
      if (dashData?.year && dashData?.month) {
        const monthStr = `${dashData.year}-${String(dashData.month).padStart(2, "0")}`;
        setPayoutMonth(monthStr);
      }
      if (dashData?.percent) {
        setPayoutPercent(dashData.percent);
      }
      setLinks((l as any).links || []);
      setCanCreateMore(Boolean((l as any).can_create_more));
      setError(null);
    } catch (e: any) {
      setError(e.message || "Failed to load affiliate data");
      show({ variant: "destructive", title: "Error", description: e.message || "Failed to load" });
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    refreshAll();
  }, []);

  const stats = useMemo(() => ({
    referrals: dash?.referrals_count || 0,
    currentCommission: dash?.amount || 0,
    pendingPayout: dash?.payouts?.pending_total || 0,
    paidPayout: dash?.payouts?.paid_total || 0,
  }), [dash]);

  async function onCreateLink() {
    try {
      await AffiliateAPI.createLink();
      await refreshAll();
      show({ variant: "success", title: "Link created" });
    } catch (e: any) {
      show({ variant: "destructive", title: "Failed to create link", description: e.message });
    }
  }

  async function onDeactivate(token: string) {
    try {
      await AffiliateAPI.deactivate(token);
      await refreshAll();
      show({ variant: "success", title: "Link deactivated" });
    } catch (e: any) {
      show({ variant: "destructive", title: "Failed to deactivate", description: e.message });
    }
  }

  async function onRotate(token: string) {
    try {
      await AffiliateAPI.rotate(token);
      await refreshAll();
      show({ variant: "success", title: "Link rotated" });
    } catch (e: any) {
      show({ variant: "destructive", title: "Failed to rotate", description: e.message });
    }
  }

  async function onRequestPayout() {
    if (!payoutMonth) {
      show({ variant: "destructive", title: "Missing month", description: "Select a month to request payment for." });
      return;
    }
    setRequestingPayout(true);
    try {
      await AffiliateAPI.requestPayout(payoutMonth, payoutPercent || 5);
      show({ variant: "success", title: "Payout requested", description: "We will review and notify you once processed." });
      await refreshAll();
    } catch (e: any) {
      show({ variant: "destructive", title: "Failed to request payout", description: e.message || "Try again later" });
    } finally {
      setRequestingPayout(false);
    }
  }

  if (loading) {
    return (
      <div className="space-y-4">
        <Skeleton className="h-8 w-64" />
        <div className="grid grid-cols-1 gap-4 md:grid-cols-4">
          {Array.from({ length: 4 }).map((_, i) => (
            <Skeleton key={i} className="h-24" />
          ))}
        </div>
        <Skeleton className="h-8 w-40" />
        <Skeleton className="h-64" />
      </div>
    );
  }

  if (error) {
    return <div className="text-sm text-red-600">{error}</div>;
  }

  const monthLabel = dash?.month ? `${String(dash.month).padStart(2, "0")}/${dash?.year}` : "Current period";

  return (
    <div className="relative -mx-4 min-h-screen overflow-hidden bg-gradient-to-br from-emerald-50 via-white to-emerald-100 sm:mx-0">
      <div className="pointer-events-none absolute -top-32 right-1/3 h-72 w-72 rounded-full bg-emerald-200/40 blur-3xl" />
      <div className="pointer-events-none absolute bottom-0 left-1/2 h-96 w-96 -translate-x-1/2 rounded-full bg-emerald-100/50 blur-3xl" />

      <div className="relative mx-auto max-w-6xl space-y-8 px-4 py-8 sm:px-6 lg:px-8">
        <div className="grid gap-6 lg:grid-cols-[minmax(0,2fr),minmax(280px,1fr)]">
          <Card className="border border-emerald-100/60 bg-white/85 shadow-xl backdrop-blur">
            <CardHeader className="mb-0 space-y-3 pb-0">
              <div className="inline-flex items-center gap-2 text-xs font-semibold uppercase tracking-widest text-emerald-600">
                <Sparkles className="h-4 w-4" /> Affiliate hub
              </div>
              <CardTitle className="text-3xl text-emerald-900 md:text-4xl">Grow your partner revenue</CardTitle>
              <p className="text-sm text-emerald-700/80">
                Track referrals, monitor commissions, and request payouts with a layout tuned for high-performing affiliates.
              </p>
            </CardHeader>
            <CardContent className="space-y-6 pt-6">
              <div className="grid gap-3 sm:grid-cols-3">
                <HighlightPill label="Active pharmacies" value={(dash?.tenants?.length || 0).toLocaleString()} />
                <HighlightPill label="This month" value={formatCurrency(stats.currentCommission)} />
                <HighlightPill label="Pending payout" value={formatCurrency(stats.pendingPayout)} tone="warning" />
              </div>
              <div className="grid gap-4 border-t border-emerald-50 pt-6 md:grid-cols-[minmax(0,1fr),auto] md:items-center">
                <div className="space-y-2">
                  <p className="text-xs font-medium uppercase tracking-wide text-emerald-500">Current period</p>
                  <p className="text-2xl font-semibold text-emerald-900">{monthLabel}</p>
                  <p className="text-sm text-emerald-700/80">
                    Commission rate <span className="font-semibold text-emerald-600">{dash?.percent ?? payoutPercent}%</span> · Earned
                    <span className="font-semibold text-emerald-600"> {formatCurrency(stats.currentCommission)}</span>
                  </p>
                </div>
                <Button
                  onClick={onCreateLink}
                  disabled={!canCreateMore}
                  className="h-12 rounded-xl bg-emerald-600 px-6 text-sm font-semibold text-white shadow-lg transition hover:bg-emerald-700 disabled:cursor-not-allowed disabled:bg-emerald-200"
                >
                  {canCreateMore ? "Create new referral link" : "Max 2 active links reached"}
                </Button>
              </div>
            </CardContent>
          </Card>

          <div className="space-y-4">
            <Card className="relative overflow-hidden border-none bg-gradient-to-br from-emerald-500 via-emerald-600 to-emerald-700 text-white shadow-xl">
              <div className="absolute right-0 top-0 h-28 w-28 rounded-bl-full bg-white/10" />
              <CardHeader className="mb-0 space-y-3 pb-0">
                <CardTitle className="text-lg font-semibold text-white">Commission snapshot</CardTitle>
                <p className="text-xs text-emerald-100/80">Earnings update live for {monthLabel.toLowerCase()}.</p>
              </CardHeader>
              <CardContent className="relative space-y-3 pt-4">
                <div className="text-3xl font-semibold">{formatCurrency(stats.currentCommission)}</div>
                <div className="flex items-center gap-2 text-xs text-emerald-100/90">
                  <Wallet className="h-4 w-4" /> Commission rate {dash?.percent ?? payoutPercent}%
                </div>
                <div className="rounded-xl bg-white/15 p-3 text-xs text-emerald-50">
                  Pending review: <span className="font-semibold">{formatCurrency(stats.pendingPayout)}</span>
                </div>
              </CardContent>
            </Card>

            <Card className="border border-emerald-100/50 bg-white/85 shadow-md backdrop-blur">
              <CardHeader className="mb-0 space-y-2 pb-0">
                <CardTitle className="text-base text-emerald-900">Quick actions</CardTitle>
                <p className="text-xs text-emerald-700/80">Keep your affiliate presence energized.</p>
              </CardHeader>
              <CardContent className="space-y-3 pt-4">
                <ul className="space-y-3 text-sm text-emerald-700">
                  <li className="flex items-start gap-3 rounded-2xl border border-emerald-100 bg-white/80 p-3">
                    <span className="mt-1 inline-flex h-6 w-6 items-center justify-center rounded-full bg-emerald-100 text-xs font-semibold text-emerald-600">
                      1
                    </span>
                    Share your strongest referral link on social to capture weekend traffic.
                  </li>
                  <li className="flex items-start gap-3 rounded-2xl border border-emerald-100 bg-white/80 p-3">
                    <span className="mt-1 inline-flex h-6 w-6 items-center justify-center rounded-full bg-emerald-100 text-xs font-semibold text-emerald-600">
                      2
                    </span>
                    Check in with newly referred pharmacies to help them onboard faster.
                  </li>
                  <li className="flex items-start gap-3 rounded-2xl border border-emerald-100 bg-white/80 p-3">
                    <span className="mt-1 inline-flex h-6 w-6 items-center justify-center rounded-full bg-emerald-100 text-xs font-semibold text-emerald-600">
                      3
                    </span>
                    Rotate inactive links to keep your codes safe and exclusive.
                  </li>
                </ul>
              </CardContent>
            </Card>
          </div>
        </div>

        <div className="grid gap-4 sm:grid-cols-2 xl:grid-cols-4">
          <CardStat
            title="Referrals"
            value={stats.referrals.toLocaleString()}
            description="Pharmacies onboarded through your links"
            icon={<Users className="h-5 w-5" />}
          />
          <CardStat
            title="Current commission"
            value={formatCurrency(stats.currentCommission)}
            description={`Earnings for ${monthLabel.toLowerCase()}`}
            icon={<Wallet className="h-5 w-5" />}
          />
          <CardStat
            title="Pending payouts"
            value={formatCurrency(stats.pendingPayout)}
            description="Awaiting manual approval"
            icon={<DollarSign className="h-5 w-5" />}
          />
          <CardStat
            title="Paid to date"
            value={formatCurrency(stats.paidPayout)}
            description="Total cleared to your account"
            icon={<Sparkles className="h-5 w-5" />}
          />
        </div>

        <div className="grid gap-4 lg:grid-cols-2">
          <Card className="border border-emerald-100/60 bg-white/85 shadow-md backdrop-blur">
            <CardHeader className="mb-0 space-y-1 pb-0">
              <CardTitle className="text-lg text-emerald-900">This month at a glance</CardTitle>
              <p className="text-xs text-emerald-700/80">Key activity in {monthLabel.toLowerCase()}.</p>
            </CardHeader>
            <CardContent className="space-y-4 pt-5">
              <div className="grid gap-3 sm:grid-cols-3">
                <MiniStat label="Commission rate" value={`${dash?.percent ?? payoutPercent}%`} />
                <MiniStat label="Earned" value={formatCurrency(dash?.amount || 0)} />
                <MiniStat label="New pharmacies" value={(dash?.tenants?.length || 0).toString()} />
              </div>
              <div className="rounded-2xl border border-dashed border-emerald-200 bg-emerald-50/70 p-4">
                <p className="text-xs font-medium text-emerald-700/80">Tenant IDs credited this month</p>
                <div className="mt-3 flex max-h-32 flex-wrap gap-2 overflow-auto pr-1">
                  {(dash?.tenants || []).length === 0 ? (
                    <span className="rounded-full bg-white px-3 py-1 text-xs font-medium text-emerald-600 shadow-sm">
                      No pharmacies have been credited yet.
                    </span>
                  ) : (
                    dash?.tenants?.map((tenant) => (
                      <span key={tenant} className="rounded-full bg-white px-3 py-1 text-xs font-medium text-emerald-700 shadow-sm">
                        {tenant}
                      </span>
                    ))
                  )}
                </div>
              </div>
            </CardContent>
          </Card>

          <Card className="border border-emerald-100/60 bg-white/85 shadow-md backdrop-blur">
            <CardHeader className="mb-0 space-y-1 pb-0">
              <CardTitle className="text-lg text-emerald-900">Request a payout</CardTitle>
              <p className="text-xs text-emerald-700/80">Submit a commission payout request in minutes.</p>
            </CardHeader>
            <CardContent className="space-y-3 pt-5">
              <div className="space-y-2">
                <label className="text-xs font-medium text-emerald-600">Month (YYYY-MM)</label>
                <Input
                  value={payoutMonth}
                  onChange={(e) => setPayoutMonth(e.target.value)}
                  placeholder="2025-10"
                  className="h-11 rounded-xl border-emerald-200 bg-white/85 focus:border-emerald-400 focus:ring-emerald-400"
                />
              </div>
              <div className="space-y-2">
                <label className="text-xs font-medium text-emerald-600">Commission percent</label>
                <Input
                  type="number"
                  min={1}
                  max={20}
                  step={0.5}
                  value={payoutPercent}
                  onChange={(e) => setPayoutPercent(Number(e.target.value))}
                  className="h-11 rounded-xl border-emerald-200 bg-white/85 focus:border-emerald-400 focus:ring-emerald-400"
                />
              </div>
              <Button
                onClick={onRequestPayout}
                disabled={requestingPayout}
                className="w-full h-11 rounded-xl bg-emerald-600 text-sm font-semibold text-white shadow-lg transition hover:bg-emerald-700 disabled:cursor-not-allowed disabled:bg-emerald-200"
              >
                {requestingPayout ? "Submitting..." : "Submit payout request"}
              </Button>
              <p className="text-xs text-emerald-600/80">
                Our finance team reviews payout requests within two business days. You&apos;ll get an email once it&apos;s processed.
              </p>
            </CardContent>
          </Card>
        </div>

        <Card className="border border-emerald-100/60 bg-white/85 shadow-md backdrop-blur">
          <CardHeader className="mb-0 flex flex-col gap-2 border-b border-emerald-100/70 bg-emerald-50/80 px-6 py-5 sm:flex-row sm:items-center sm:justify-between">
            <div className="flex items-center gap-2 text-sm font-semibold text-emerald-900">
              <span className="inline-flex h-9 w-9 items-center justify-center rounded-full bg-emerald-100 text-emerald-600">
                <Link2 className="h-4 w-4" />
              </span>
              Referral links
            </div>
            <div className="text-xs font-medium text-emerald-600">You can keep up to 2 links active at once.</div>
          </CardHeader>
          <CardContent className="divide-y divide-emerald-100/70 px-0">
            {links.map((l) => (
              <div key={l.token} className="flex flex-col gap-4 px-6 py-5 sm:flex-row sm:items-center sm:justify-between">
                <div className="space-y-2">
                  <div className="flex items-center gap-2 text-xs font-medium text-emerald-600">
                    <span
                      className={`inline-flex items-center gap-2 rounded-full px-3 py-1 text-xs font-semibold ${
                        l.active ? "bg-emerald-100 text-emerald-700" : "bg-gray-100 text-gray-600"
                      }`}
                    >
                      <span className="h-2.5 w-2.5 rounded-full bg-current" />
                      {l.active ? "Active" : "Inactive"}
                    </span>
                    <span className="font-mono text-[11px] uppercase tracking-widest text-emerald-500/80">{l.token.slice(0, 10)}</span>
                  </div>
                  <div className="flex items-center gap-2 text-sm font-medium text-emerald-900">
                    <Link2 className="h-4 w-4 text-emerald-500" />
                    <span className="break-all font-mono text-xs text-emerald-800/90">{l.url}</span>
                  </div>
                </div>
                <div className="flex flex-wrap gap-2">
                  <Button
                    size="sm"
                    variant="outline"
                    className="rounded-lg border-emerald-200 text-emerald-700 hover:bg-emerald-50"
                    onClick={() => onCopy(l.url)}
                  >
                    <Copy className="mr-2 h-4 w-4" /> Copy
                  </Button>
                  <Button
                    size="sm"
                    variant="outline"
                    className="rounded-lg border-emerald-200 text-emerald-700 hover:bg-emerald-50 disabled:border-gray-200 disabled:text-gray-400"
                    onClick={() => onDeactivate(l.token)}
                    disabled={!l.active}
                  >
                    Deactivate
                  </Button>
                  <Button
                    size="sm"
                    variant="outline"
                    className="rounded-lg border-emerald-200 text-emerald-700 hover:bg-emerald-50"
                    onClick={() => onRotate(l.token)}
                  >
                    Rotate
                  </Button>
                </div>
              </div>
            ))}
            {links.length === 0 && (
              <div className="px-6 py-10 text-center text-sm text-emerald-700/70">
                No referral links yet. Use the button above to create one and start sharing.
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
}

function CardStat({
  title,
  value,
  description,
  icon,
}: {
  title: string;
  value: React.ReactNode;
  description?: string;
  icon?: React.ReactNode;
}) {
  return (
    <Card className="border border-emerald-100/60 bg-white/85 shadow-md transition hover:-translate-y-0.5 hover:shadow-lg">
      <CardContent className="flex items-start justify-between gap-3 p-5">
        <div>
          <p className="text-xs font-semibold uppercase tracking-wide text-emerald-500">{title}</p>
          <p className="mt-3 text-2xl font-semibold text-emerald-900">{value}</p>
          {description && <p className="mt-3 text-xs text-emerald-700/80">{description}</p>}
        </div>
        {icon && <span className="inline-flex h-10 w-10 items-center justify-center rounded-xl bg-emerald-50 text-emerald-600">{icon}</span>}
      </CardContent>
    </Card>
  );
}

function MiniStat({ label, value }: { label: string; value: React.ReactNode }) {
  return (
    <Card className="border border-emerald-100/60 bg-white/80 shadow-sm">
      <CardContent className="p-4">
        <p className="text-[11px] font-semibold uppercase tracking-widest text-emerald-500">{label}</p>
        <p className="mt-2 text-lg font-semibold text-emerald-900">{value}</p>
      </CardContent>
    </Card>
  );
}

function HighlightPill({
  label,
  value,
  tone = "default",
}: {
  label: string;
  value: React.ReactNode;
  tone?: "default" | "warning";
}) {
  const classes =
    tone === "warning"
      ? "border-amber-100 bg-amber-50 text-amber-700"
      : "border-emerald-100 bg-emerald-50 text-emerald-700";
  return (
    <div className={`rounded-2xl border px-4 py-3 shadow-sm ${classes}`}>
      <p className="text-[11px] uppercase tracking-widest opacity-80">{label}</p>
      <p className="mt-2 text-lg font-semibold">{value}</p>
    </div>
  );
}
